/**
 * API Client - Axios ูุน ุงูุชูููู ุงูุฃุณุงุณู
 * ูุชุถูู interceptors ููู Token ู Error handling
 */

import axios, { AxiosError, AxiosInstance, InternalAxiosRequestConfig } from "axios";

// ุนููุงู ุงูู Backend API
const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5250/api";

// ููุงุชูุญ LocalStorage
const ACCESS_TOKEN_KEY = "event_meena_access_token";
const REFRESH_TOKEN_KEY = "event_meena_refresh_token";

/**
 * ุงุณุชุฌุงุจุฉ ุงูู API ุงูููุญุฏุฉ ูู Backend
 */
export interface ApiResponse<T> {
  success: boolean;
  data: T | null;
  message: string | null;
  errors: string[] | null;
}

/**
 * ุฎุทุฃ API ูุฎุตุต
 */
export class ApiError extends Error {
  status: number;
  errors: string[];

  constructor(message: string, status: number, errors: string[] = []) {
    super(message);
    this.name = "ApiError";
    this.status = status;
    this.errors = errors;
  }
}

/**
 * ุฅุฏุงุฑุฉ ุงูุชูููุงุช
 */
export const tokenManager = {
  getAccessToken: (): string | null => {
    if (typeof window === "undefined") return null;
    return localStorage.getItem(ACCESS_TOKEN_KEY);
  },

  getRefreshToken: (): string | null => {
    if (typeof window === "undefined") return null;
    return localStorage.getItem(REFRESH_TOKEN_KEY);
  },

  setTokens: (accessToken: string, refreshToken: string): void => {
    if (typeof window === "undefined") return;
    localStorage.setItem(ACCESS_TOKEN_KEY, accessToken);
    localStorage.setItem(REFRESH_TOKEN_KEY, refreshToken);
  },

  clearTokens: (): void => {
    if (typeof window === "undefined") return;
    localStorage.removeItem(ACCESS_TOKEN_KEY);
    localStorage.removeItem(REFRESH_TOKEN_KEY);
  },

  hasValidToken: (): boolean => {
    return !!tokenManager.getAccessToken();
  },
};

/**
 * ุฅูุดุงุก Axios Instance
 */
const createApiClient = (): AxiosInstance => {
  const client = axios.create({
    baseURL: API_BASE_URL,
    headers: {
      "Content-Type": "application/json",
    },
    timeout: 30000, // 30 ุซุงููุฉ
  });

  // Request Interceptor - ุฅุถุงูุฉ ุงูุชููู ููู ุทูุจ
  client.interceptors.request.use(
    (config: InternalAxiosRequestConfig) => {
      const token = tokenManager.getAccessToken();
      if (token && config.headers) {
        config.headers.Authorization = `Bearer ${token}`;
      }
      return config;
    },
    (error) => Promise.reject(error)
  );

  // Response Interceptor - ูุนุงูุฌุฉ ุงูุฃุฎุทุงุก ูุชุฌุฏูุฏ ุงูุชููู
  client.interceptors.response.use(
    (response) => response,
    async (error: AxiosError<ApiResponse<unknown>>) => {
      const originalRequest = error.config as InternalAxiosRequestConfig & {
        _retry?: boolean;
      };

      // ุฅุฐุง ูุงู ุงูุฎุทุฃ 401 ููู ูุญุงูู ุงูุชุฌุฏูุฏ ุจุนุฏ
      if (error.response?.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true;

        const refreshToken = tokenManager.getRefreshToken();
        if (refreshToken) {
          try {
            // ูุญุงููุฉ ุชุฌุฏูุฏ ุงูุชููู
            const response = await axios.post<ApiResponse<{
              accessToken: string;
              refreshToken: string;
            }>>(`${API_BASE_URL}/Auth/refresh-token`, {
              refreshToken,
            });

            if (response.data.success && response.data.data) {
              const { accessToken, refreshToken: newRefreshToken } = response.data.data;
              tokenManager.setTokens(accessToken, newRefreshToken);

              // ุฅุนุงุฏุฉ ุงููุญุงููุฉ ูุน ุงูุชููู ุงูุฌุฏูุฏ
              if (originalRequest.headers) {
                originalRequest.headers.Authorization = `Bearer ${accessToken}`;
              }
              return client(originalRequest);
            }
          } catch {
            // ูุดู ุชุฌุฏูุฏ ุงูุชููู - ุชุณุฌูู ุงูุฎุฑูุฌ
            tokenManager.clearTokens();
            if (typeof window !== "undefined") {
              window.location.href = "/login";
            }
          }
        }
      }

      // ุชุญููู ุงูุฎุทุฃ ุฅูู ApiError
      const apiError = createApiError(error);
      return Promise.reject(apiError);
    }
  );

  return client;
};

/**
 * ุฅูุดุงุก ApiError ูู AxiosError
 */
const createApiError = (error: AxiosError<ApiResponse<unknown>>): ApiError => {
  if (error.response) {
    const { data, status } = error.response;
    const message = data?.message || getErrorMessage(status);
    const errors = data?.errors || [];
    return new ApiError(message, status, errors);
  }

  if (error.request) {
    return new ApiError("ูุง ูููู ุงูุงุชุตุงู ุจุงูุฎุงุฏู. ุชุฃูุฏ ูู ุงุชุตุงูู ุจุงูุฅูุชุฑูุช.", 0, []);
  }

  return new ApiError(error.message || "ุญุฏุซ ุฎุทุฃ ุบูุฑ ูุชููุน", 0, []);
};

/**
 * ุงูุญุตูู ุนูู ุฑุณุงูุฉ ุฎุทุฃ ุจูุงุกู ุนูู ุงูููุฏ
 */
const getErrorMessage = (status: number): string => {
  switch (status) {
    case 400:
      return "ุทูุจ ุบูุฑ ุตุงูุญ. ุชุญูู ูู ุงูุจูุงูุงุช ุงููุฏุฎูุฉ.";
    case 401:
      return "ุบูุฑ ูุตุฑุญ. ูุฑุฌู ุชุณุฌูู ุงูุฏุฎูู ูุฑุฉ ุฃุฎุฑู.";
    case 403:
      return "ุบูุฑ ูุณููุญ. ููุณ ูุฏูู ุตูุงุญูุฉ ูููุตูู.";
    case 404:
      return "ุบูุฑ ููุฌูุฏ. ุงูููุฑุฏ ุงููุทููุจ ุบูุฑ ูุชุงุญ.";
    case 409:
      return "ุชุนุงุฑุถ. ุงูุจูุงูุงุช ููุฌูุฏุฉ ูุณุจูุงู.";
    case 422:
      return "ุจูุงูุงุช ุบูุฑ ุตุงูุญุฉ. ุชุญูู ูู ุงููุฏุฎูุงุช.";
    case 429:
      return "ุทูุจุงุช ูุซูุฑุฉ. ุงูุชุธุฑ ููููุงู ุซู ุญุงูู ูุฑุฉ ุฃุฎุฑู.";
    case 500:
      return "ุฎุทุฃ ูู ุงูุฎุงุฏู. ุญุงูู ูุฑุฉ ุฃุฎุฑู ูุงุญูุงู.";
    case 502:
    case 503:
    case 504:
      return "ุงูุฎุฏูุฉ ุบูุฑ ูุชุงุญุฉ ุญุงููุงู. ุญุงูู ูุฑุฉ ุฃุฎุฑู ูุงุญูุงู.";
    default:
      return "ุญุฏุซ ุฎุทุฃ ุบูุฑ ูุชููุน.";
  }
};

// ุฅูุดุงุก ูุชุตุฏูุฑ ุงูู API Client
export const apiClient = createApiClient();

// ุชุตุฏูุฑ ุงูุชุฑุงุถู
export default apiClient;

