/**
 * Auth Service - ุฎุฏูุฉ ุงููุตุงุฏูุฉ
 * ุงูุงุชุตุงู ุจู Backend API ูููุตุงุฏูุฉ
 */

import { apiClient, ApiResponse, tokenManager } from "../client";
import { BackendAuthResponse, BackendUserDto, mapAuthResponse, mapUser } from "../mappers";
import { LoginData, SignupData, User } from "@/types/auth";

// ============================================================
// ุฃููุงุน ุงูุงุณุชุฌุงุจุงุช
// ============================================================

interface AuthResult {
  user: User;
  token: string;
  refreshToken: string;
}

// ============================================================
// ุฎุฏูุฉ ุงููุตุงุฏูุฉ
// ============================================================

export const authService = {
  /**
   * ุชุณุฌูู ุงูุฏุฎูู
   */
  login: async (data: LoginData): Promise<AuthResult> => {
    const response = await apiClient.post<ApiResponse<BackendAuthResponse>>(
      "/Auth/login",
      {
        email: data.email,
        password: data.password,
        rememberMe: data.rememberMe || false,
      }
    );

    if (!response.data.success || !response.data.data) {
      throw new Error(response.data.message || "ูุดู ุชุณุฌูู ุงูุฏุฎูู");
    }

    const result = mapAuthResponse(response.data.data);
    
    // ุญูุธ ุงูุชูููุงุช
    tokenManager.setTokens(result.token, result.refreshToken);

    return result;
  },

  /**
   * ุฅูุดุงุก ุญุณุงุจ ุฌุฏูุฏ
   */
  register: async (data: SignupData): Promise<AuthResult> => {
    const response = await apiClient.post<ApiResponse<BackendAuthResponse>>(
      "/Auth/register",
      {
        fullName: data.name,
        email: data.email,
        password: data.password,
        confirmPassword: data.confirmPassword,
        phone: data.phone || null,
      }
    );

    if (!response.data.success || !response.data.data) {
      throw new Error(response.data.message || "ูุดู ุฅูุดุงุก ุงูุญุณุงุจ");
    }

    const result = mapAuthResponse(response.data.data);
    
    // ุญูุธ ุงูุชูููุงุช
    tokenManager.setTokens(result.token, result.refreshToken);

    return result;
  },

  /**
   * ุชุณุฌูู ุงูุฎุฑูุฌ
   */
  logout: async (): Promise<void> => {
    try {
      await apiClient.post("/Auth/logout");
    } finally {
      // ูุณุญ ุงูุชูููุงุช ูู ุฌููุน ุงูุญุงูุงุช
      tokenManager.clearTokens();
    }
  },

  /**
   * ุงูุญุตูู ุนูู ุจูุงูุงุช ุงููุณุชุฎุฏู ุงูุญุงูู
   */
  getCurrentUser: async (): Promise<User> => {
    const response = await apiClient.get<ApiResponse<BackendUserDto>>("/Auth/me");

    if (!response.data.success || !response.data.data) {
      throw new Error(response.data.message || "ูุดู ุฌูุจ ุจูุงูุงุช ุงููุณุชุฎุฏู");
    }

    return mapUser(response.data.data);
  },

  /**
   * ุชุญุฏูุซ ุงูููู ุงูุดุฎุตู
   */
  updateProfile: async (data: {
    name?: string;
    phone?: string;
    avatar?: string;
  }): Promise<User> => {
    const response = await apiClient.put<ApiResponse<BackendUserDto>>(
      "/Auth/profile",
      {
        fullName: data.name,
        phone: data.phone || null,
        profileImage: data.avatar || null,
      }
    );

    if (!response.data.success || !response.data.data) {
      throw new Error(response.data.message || "ูุดู ุชุญุฏูุซ ุงูููู ุงูุดุฎุตู");
    }

    return mapUser(response.data.data);
  },

  /**
   * ุชุบููุฑ ูููุฉ ุงููุฑูุฑ
   */
  changePassword: async (data: {
    currentPassword: string;
    newPassword: string;
    confirmNewPassword: string;
  }): Promise<void> => {
    const response = await apiClient.post<ApiResponse<null>>("/Auth/change-password", data);

    if (!response.data.success) {
      throw new Error(response.data.message || "ูุดู ุชุบููุฑ ูููุฉ ุงููุฑูุฑ");
    }
  },

  /**
   * ูุณูุช ูููุฉ ุงููุฑูุฑ
   */
  forgotPassword: async (email: string): Promise<void> => {
    const response = await apiClient.post<ApiResponse<null>>("/Auth/forgot-password", {
      email,
    });

    if (!response.data.success) {
      throw new Error(response.data.message || "ูุดู ุฅุฑุณุงู ุฑุงุจุท ุงุณุชุนุงุฏุฉ ูููุฉ ุงููุฑูุฑ");
    }
  },

  /**
   * ุฅุนุงุฏุฉ ุชุนููู ูููุฉ ุงููุฑูุฑ
   */
  resetPassword: async (data: {
    token: string;
    email: string;
    newPassword: string;
  }): Promise<void> => {
    const response = await apiClient.post<ApiResponse<null>>("/Auth/reset-password", data);

    if (!response.data.success) {
      throw new Error(response.data.message || "ูุดู ุฅุนุงุฏุฉ ุชุนููู ูููุฉ ุงููุฑูุฑ");
    }
  },

  /**
   * ุงูุชุญูู ูู ุตุญุฉ ุงูุชููู
   */
  verifyToken: async (): Promise<boolean> => {
    if (!tokenManager.hasValidToken()) {
      return false;
    }

    try {
      await authService.getCurrentUser();
      return true;
    } catch {
      tokenManager.clearTokens();
      return false;
    }
  },
};

export default authService;

